import xlmOfficeView from "./office/index";
import xlmGantt from "./gantt/index";

// ,
const components = [xlmOfficeView, xlmGantt];

//  install  Vue  use 
const install = function (Vue) {
  // 
  if (install.installed) return;
  install.installed = true;
  // 
  components.map((component) => {
    Vue.component(component.name, component);
  });
};

// 
if (typeof window !== "undefined" && window.Vue) {
  install(window.Vue);
}

export default {
  //  install  Vue.use() 
  install,
  // 
  ...components,
};
